import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import sayyes from '../components/sayyes/sayyes'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: sayyes
    },
      {
          path: '/sayyes',
          name: 'sayyes',
          component: sayyes
      }
  ]
})



