<template>

    <div>
        <h1>条款</h1>
        <ol>
            <li v-for=" item in sayList">{{item}}</li>
        </ol>
        <div>
           少根筋说的话：<el-input v-model="input" placeholder="请输入对或者错"></el-input>
            结果：<p>{{reversedMessage}}</p>
        </div>

        <el-button type="text" @click="dialogVisible = true">谁是最棒的</el-button>

        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
            <span>少根筋是最棒的</span>
            <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                sayList:[
                    "少根筋永远是对的",
                    "如果错了参照第一条"
                ],
                dialogVisible: false,
                input:""
            }
        },
        methods: {
            handleClose(done) {
                this.$confirm('确认关闭？')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {});
            }
        },
        computed: {
            // 计算属性的 getter
            reversedMessage: function () {
                // `this` 指向 vm 实例
                if(this.input==""){
                    return "请注意你的言辞"
                }
                if(this.input=="对"){
                    return "你真聪明"
                }else{
                    return "请参照第一条，少根筋永远是对的"
                }

                //return this.message.split('').reverse().join('')
            }
        }
    }
</script>




